.home {
  background-color: var(--background-primary-color);

  h1 {
    font-size: clamp(3.6rem, 5vw, 6.4rem);
    line-height: initial;
    color: var(--title-primary-color);
    span {
      color: var(--title-secondary-color);
    }
  }
  &Content {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    @include media(small) {
      flex-direction: row;
      align-items: flex-start;
    }
    @include media(medium) {
      justify-content: space-around;
      padding: 5rem 2.7rem;

    }
    @include media(large) {
      justify-content: space-evenly;
    }
    @include media(extra-large) {
      padding: 6rem 24.7rem;
      justify-content: space-between;
    }
    &__entreprise {
      color: var(--title-secondary-color);
      font-weight: 500;
      font-family: "Raleway";
      @include media(medium) {
        font-size: 2.4rem;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      gap: 1.2rem;
      @include media(small) {
        gap: 1.5rem;
      }
      p {
        font-size: 1.8rem;
        @include media(small) {
          font-size: 2.2rem;
        }
        @include media(medium) {
          font-size: 2rem;
        }
        @include media(large) {
          font-size: 2.4rem;
        }
      }
    }
  }
  .social {
    justify-content: flex-start;
    gap: 2rem;

    @include media(medium) {
      justify-content: flex-start;
    }
    img {
      filter: invert(43%) sepia(7%) saturate(1152%) hue-rotate(172deg)
        brightness(91%) contrast(85%);
      &:hover {
        filter: invert(54%) sepia(76%) saturate(3306%) hue-rotate(316deg)
          brightness(100%) contrast(102%);
        transition: 0.2s;
      }
      width: 2.6rem;
      height: auto;
      /*  @include media (small){
        width: 3.0rem;
      }
      @include media(medium){
        width: 3rem;
      } */
    }
  }
  &-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
    @include media(small) {
    flex-direction: row;
      align-items: flex-start;
      gap: 3rem;
    }
    @include media(medium) {
      justify-content: flex-start;
    }
  }
  &-img {
    width: 25rem;
    @include media(small) {
      width: 30rem;
    }
    @include media(medium) {
      width: 35rem;
    }
    @include media(extra-large) {
      width: 52.2rem;
    }
  }
  .btn-secondary {
    img {
      filter: invert(43%) sepia(61%) saturate(849%) hue-rotate(305deg)
        brightness(102%) contrast(101%);
    }
  }
}
